import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import privacy from "../images/privacy.jpg"

const Privacy = () => (
  <Layout>
    <SEO title="Advertise with Bigindia.com" />
    <div className="text-gray-700 body-font">
      <div className="flex p-8 md:flex-row flex-col">
        <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0">
          <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900">
            Privacy Policy
          </h1>
          <p className="mb-5 leading-relaxed">
            At BigIndia, accessible from BigIndia.com, one of our main
            priorities is the privacy of our visitors.
          </p>

          <p className="mb-5 leading-relaxed">
            This Privacy Policy document contains types of information that is
            collected and recorded by BigIndia.com and how we use it.
          </p>

          <p className="mb-5 leading-relaxed">
            If you have additional questions or require more information about
            our Privacy Policy, do not hesitate to contact us.
          </p>

          <p className="mb-5 leading-relaxed">
            This Privacy Policy applies only to our online activities and is
            valid for visitors to our website with regards to the information
            that they shared and/or collect in BigIndia.com. This policy is not
            applicable to any information collected offline or via channels
            other than this website.
          </p>

          <p className="mb-3 text-xl font-semibold leading-relaxed">Consent</p>
          <p className="mb-5 leading-relaxed">
            By using our website, you hereby consent to our Privacy Policy and
            agree to its terms.
          </p>
          <p className="mb-5 leading-relaxed">Information we collect</p>
          <p className="mb-5 leading-relaxed">
            The personal information that you are asked to provide, and the
            reasons why you are asked to provide it, will be made clear to you
            at the point we ask you to provide your personal information.
          </p>
          <p className="mb-5 leading-relaxed">
            If you contact us directly, we may receive additional information
            about you such as your name, email address, phone number, the
            contents of the message and/or attachments you may send us, and any
            other information you may choose to provide.
          </p>
          <p className="mb-5 leading-relaxed">
            When you register for an Account, we may ask for your contact
            information, including items such as name, company name, address,
            email address, and telephone number.
          </p>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            How we use your information
          </p>
          <p className="mb-5 leading-relaxed font-semibold">
            We use the information we collect in various ways, including to:
          </p>
          <ul className="mb-5 ml-3 leading-relaxed">
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Provide,
              operate, and maintain our website
            </li>
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Improve,
              personalize, and expand our website
            </li>
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Understand and
              analyze how you use our website
            </li>
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Develop new
              products, services, features, and functionality
            </li>
          </ul>
          <p className="mb-5 leading-relaxed">
            Communicate with you, either directly or through one of our
            partners, including for customer service, to provide you with
            updates and other information relating to the website, and for
            marketing and promotional purposes
          </p>
          <ul className="mb-5 ml-3 leading-relaxed">
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Send you emails
            </li>
            <li className="py-1">
              <ion-icon name="caret-forward-outline"></ion-icon> Find and
              prevent fraud
            </li>
          </ul>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            Log Files
          </p>
          <p className="mb-5 leading-relaxed">
            BigIndia.com follows a standard procedure of using log files. These
            files log visitors when they visit websites. All hosting companies
            do this and a part of hosting services' analytics. The information
            collected by log files include internet protocol (IP) addresses,
            browser type, Internet Service Provider (ISP), date and time stamp,
            referring/exit pages, and possibly the number of clicks. These are
            not linked to any information that is personally identifiable. The
            purpose of the information is for analyzing trends, administering
            the site, tracking users' movement on the website, and gathering
            demographic information.
          </p>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            Cookies and Web Beacons
          </p>
          <p className="mb-5 leading-relaxed">
            Like any other website, BigIndia.com uses 'cookies'. These cookies
            are used to store information including visitors' preferences, and
            the pages on the website that the visitor accessed or visited. The
            information is used to optimize the users' experience by customizing
            our web page content based on visitors' browser type and/or other
            information.
          </p>
          <p className="mb-5 leading-relaxed">
            For more general information on cookies, please read "What Are
            Cookies" from Cookie Consent.
          </p>
          <p className="mb-5 leading-relaxed font-semibold">
            Google DoubleClick DART Cookie
          </p>

          <p className="mb-5 leading-relaxed">
            Google is one of a third-party vendor on our site. It also uses
            cookies, known as DART cookies, to serve ads to our site visitors
            based upon their visit to sites on the internet. However, visitors
            may choose to decline the use of DART cookies by visiting the Google
            ad and content network Privacy Policy at the following URL –
            https://policies.google.com/technologies/ads
          </p>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            Our Advertising Partners
          </p>
          <p className="mb-5 leading-relaxed">
            Some of advertisers on our site may use cookies and web beacons. Our
            advertising partners are listed below. Each of our advertising
            partners has their own Privacy Policy for their policies on user
            data. For easier access, we hyperlinked to their Privacy Policies
            below.
          </p>
          <p className="mb-2 leading-relaxed font-semibold">Google</p>
          <p className="mb-5 leading-relaxed">
            https://policies.google.com/technologies/ads
          </p>
          <p className="mb-5 leading-relaxed font-semibold">
            Advertising Partners Privacy Policies
          </p>
          <p className="mb-5 leading-relaxed">
            You may consult this list to find the Privacy Policy for each of the
            advertising partners of BigIndia.com.
          </p>
          <p className="mb-5 leading-relaxed">
            Third-party ad servers or ad networks uses technologies like
            cookies, JavaScript, or Web Beacons that are used in their
            respective advertisements and links that appear on BigIndia.com,
            which are sent directly to users' browser. They automatically
            receive your IP address when this occurs. These technologies are
            used to measure the effectiveness of their advertising campaigns
            and/or to personalize the advertising content that you see on
            websites that you visit.
          </p>
          <p className="mb-5 leading-relaxed">
            Note that BigIndia.com has no access to or control over these
            cookies that are used by third-party advertisers.
          </p>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            Third Party Privacy Policies
          </p>
          <p className="mb-5 leading-relaxed">
            BigIndia.com's Privacy Policy does not apply to other advertisers or
            websites. Thus, we are advising you to consult the respective
            Privacy Policies of these third-party ad servers for more detailed
            information. It may include their practices and instructions about
            how to opt-out of certain options.
          </p>
          <p className="mb-5 leading-relaxed">
            You can choose to disable cookies through your individual browser
            options. To know more detailed information about cookie management
            with specific web browsers, it can be found at the browsers'
            respective websites.
          </p>
          <p className="mb-5 leading-relaxed">
            CCPA Privacy Rights (Do Not Sell My Personal Information)
          </p>
          <p className="mb-5 leading-relaxed">
            Under the CCPA, among other rights, California consumers have the
            right to:
          </p>
          <p className="mb-5 leading-relaxed">
            Request that a business that collects a consumer's personal data
            disclose the categories and specific pieces of personal data that a
            business has collected about consumers.
          </p>
          <p className="mb-5 leading-relaxed">
            Request that a business delete any personal data about the consumer
            that a business has collected.
          </p>
          <p className="mb-5 leading-relaxed">
            Request that a business that sells a consumer's personal data, not
            sell the consumer's personal data.
          </p>
          <p className="mb-5 leading-relaxed">
            If you make a request, we have one month to respond to you. If you
            would like to exercise any of these rights, please contact us.
          </p>
          <p className="mb-5 leading-relaxed text-xl font-semibold">
            GDPR Data Protection Rights
          </p>
          <p className="mb-5 leading-relaxed">
            We would like to make sure you are fully aware of all of your data
            protection rights. Every user is entitled to the following:
          </p>
          <p className="mb-5 leading-relaxed">
            The right to access – You have the right to request copies of your
            personal data. We may charge you a small fee for this service.
          </p>
          <p className="mb-5 leading-relaxed">
            The right to rectification – You have the right to request that we
            correct any information you believe is inaccurate.{" "}
          </p>
          <p className="mb-5 leading-relaxed">
            You also have the right to request that we complete the information
            you believe is incomplete.
          </p>
          <p className="mb-5 leading-relaxed">
            The right to erasure – You have the right to request that we erase
            your personal data, under certain conditions.
          </p>
          <p className="mb-5 leading-relaxed">
            The right to restrict processing – You have the right to request
            that we restrict the processing of your personal data, under certain
            conditions.
          </p>
          <p className="mb-5 leading-relaxed">
            The right to object to processing – You have the right to object to
            our processing of your personal data, under certain conditions.
          </p>
          <p className="mb-5 leading-relaxed">
            The right to data portability – You have the right to request that
            we transfer the data that we have collected to another organization,
            or directly to you, under certain conditions.
          </p>
          <p className="mb-5 leading-relaxed">
            If you make a request, we have one month to respond to you. If you
            would like to exercise any of these rights, please contact us.
          </p>
          <p className="mb-5 leading-relaxed font-semibold">
            Children's Information
          </p>
          <p className="mb-5 leading-relaxed">
            Another part of our priority is adding protection for children while
            using the internet.{" "}
          </p>
          <p className="mb-5 leading-relaxed">
            We encourage parents and guardians to observe, participate in,
            and/or monitor and guide their online activity.
          </p>
          <p className="mb-5 leading-relaxed">
            BigIndia.com does not knowingly collect any Personal Identifiable
            Information from children under the age of 13.{" "}
          </p>
          <p className="mb-5 leading-relaxed">
            If you think that your child provided this kind of information on
            our website, we strongly encourage you to contact us immediately and
            we will do our best efforts to promptly remove such information from
            our records.
          </p>
          <p className="mb-5 leading-relaxed font-semibold">Questions</p>
          <p className="mb-5 leading-relaxed">
            Please contact us regarding any questions, clarifications, or
            grievances.
          </p>
        </div>
        <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
        <img src={privacy} alt="privacy" />
        </div>
      </div>
    </div>
  </Layout>
)

export default Privacy
